@import url('https://fonts.googleapis.com/css2?family=Syne:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap');


html, body {
  font-family: "Inter", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: hidden;
  display: flex;
  height: 100%;
}

h1, h2, h3, h4, h6 {
  font-family: "Inter", sans-serif;
  margin-top: 20px;
  margin-left:20px;
  padding: 0;
}

h5 {
  font-family: "Inter", sans-serif;
  margin: 5px;
  padding: 0;
}

.text-container {
  max-width: 100ch;
}

.text {
  margin: 20px;
}

#grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0px;
  max-width: 100%;
}

#left-side {
  background-color: #f5f5f5;
  padding: 20px;
}

/* lnos --------------------------------------------------------------------*/
#lnos-container {
  background-color: white; 
  padding: 0px;
  margin-top: auto;
}   

#lnos-chart {
  overflow-x: hidden;
}

.hover {
  cursor: pointer;
}

.country:hover ~ .country,
.country:not(:hover) {
  opacity: 0.5; 
}

iframe{
  position: fixed;
  bottom:20px;
  max-width: 18%;
  left:20px;
  
}

strong{
  color:#415d9b;
}